import './App.css';
import $ from 'jquery';

import React, { useState, useEffect } from "react";
import DatePicker, { registerLocale } from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import ko from 'date-fns/locale/ko';

import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import ListGroup from 'react-bootstrap/ListGroup';

import PriceTable from './PriceTable';

registerLocale('ko', ko);

function App() {

  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());

  const [minTime, setMinTime] = useState(new Date());
  const [maxTime, setMaxTime] = useState(new Date());
  const [excludedTime, setExcludedTime] = useState([]);

  const [days, setDays] = useState(0);
  const [hours, setHours] = useState(0);
  const [selected, setSelected] = useState(false);

  function dateCalculator(startDate, endDate) {
    const totalHours = (endDate - startDate) / (1000 * 3600)
    const calculatedDays = Math.floor(totalHours / 24)
    const calculatedHours = (totalHours % 24).toFixed(1);

    if (calculatedHours >= 7) {
      setDays(calculatedDays+1);
      setHours(0);
    } else {
      setDays(calculatedDays)
      setHours(calculatedHours)
    }
  }

  useEffect(() => {
    $( function() {
      $( "#datepicker" ).datepicker();
    } );

    startDate.setDate(startDate.getDate()+2);
    startDate.setHours(10);
    startDate.setMinutes(0);

    endDate.setDate(startDate.getDate()+2);
    endDate.setHours(10);
    endDate.setMinutes(0);

    minTime.setHours(8);
    minTime.setMinutes(0);

    maxTime.setHours(18);
    maxTime.setMinutes(30);

    const notAvailableTime = []
    for (let count = 3; count < 8; count += 0.5) {
      let time = new Date()
      if (count % 1 !== 0) {
        time.setHours(Math.floor(count), 30, 0);
      } else {
        time.setHours(Math.floor(count), 0, 0);
      }
      notAvailableTime.push(time);
    };
    setExcludedTime(notAvailableTime);
  }, [])

  useEffect(() => {
    dateCalculator(startDate, endDate)
    if (days === 0 && hours < 7 && selected) {
      alert("  7   .");
      endDate.setDate(startDate.getDate()+1);
    }
  }, [startDate, endDate])

  useEffect(() => {
    if (startDate > endDate) {
      alert("      .")
      setEndDate(startDate)
    }
  }, [endDate])

  return (
    <div className="App">
      <meta charSet="utf-8" />
      <title>  </title>
      <Navbar style={{ backgroundColor:'#343A3F' }}>
        <Container>
          <Navbar.Brand>
            <span style={{ color:'#FFFFFF' }}>  </span>
            <span style={{ fontSize: "15px", color:'#FFFFFF' }}></span>
          </Navbar.Brand>
        </Container>
      </Navbar>
      <div className='body-section'>
        <div className='body-row'>
          <ListGroup style={{ marginRight: '50px' }}>
            <ListGroup.Item className='list-group-item'>
              <h6></h6>
              <DatePicker
                className='date-picker'
                selected={startDate}
                onChange={(date) => {
                  setStartDate(date);
                }}
                minDate={startDate}
                popperProps={{
                  positionFixed: true
                }}
                popperModifiers={{
                  preventOverflow: {
                    enabled: true,
                }}}
                dateFormat=" yyyy-MM-dd"
                locale='ko' />

            </ListGroup.Item>
            <ListGroup.Item className='list-group-item'>
              <h6></h6>
              <DatePicker
                className='date-picker'
                selected={startDate}
                onChange={(date) => {
                  setStartDate(date);
                  setSelected(true);
                }}
                showTimeSelect
                showTimeSelectOnly
                dateFormat=" HH:mm"
                minTime={minTime}
                maxTime={maxTime}
                timeIntervals={30}
                popperProps={{
                  positionFixed: true
                }}
                popperModifiers={{
                  preventOverflow: {
                    enabled: true,
                }}}
                timeCaption=""
                locale='ko' />
            </ListGroup.Item>
            <ListGroup.Item className='list-group-item'>
              <h6></h6>
              <DatePicker
                className='date-picker'
                selected={endDate}
                onSelect={(date) => {setEndDate(date)}}
                minDate={startDate}
                popperProps={{
                  positionFixed: true
                }}
                popperModifiers={{
                  preventOverflow: {
                    enabled: true,
                }}}
                dateFormat=" yyyy-MM-dd"
                locale='ko' />
            </ListGroup.Item>
            <ListGroup.Item className='list-group-item'>
              <h6></h6>
              <DatePicker
                className='date-picker'
                selected={endDate}
                onChange={(date) => {
                  setEndDate(date)
                  setSelected(true)
                }}
                showTimeSelect
                showTimeSelectOnly
                timeIntervals={30}
                excludeTimes={excludedTime}
                popperProps={{
                  positionFixed: true
                }}
                popperModifiers={{
                  preventOverflow: {
                    enabled: true,
                }}}
                timeCaption=""
                dateFormat=" HH:mm"
                locale='ko' />
            </ListGroup.Item>
            <ListGroup.Item className='list-group-item' style={{ backgroundColor: '#F5F5F5' }}>
              <h6></h6>
              <div style={{ fontWeight:'800' }}>
                {days}
                {(hours>0.5 && hours<7 && days<3) ?<span>+ {Math.floor(hours)}</span> : null}
                {(hours && hours<7 && days<3 && hours%1 !== 0 ? <span> 30</span>:null)}
              </div>
            </ListGroup.Item>
          </ListGroup>
          <br />
          <div style={{ margin: 'auto' }}>
            <div style={{ textAlign:'left', color: 'red', fontWeight:600 }}>
              <div>*    .</div>
              <div>*   <a href='https://cafe.naver.com/guamfree/335157'> </a> .</div>
              <div>*  30       . &lt;<a href='mailto:tillpark3@naver.com'>tillpark3@naver.com</a>&gt;</div>
            </div>
            <br />
            <PriceTable
              category=""
              cars=", "
              reservePrice={10} // 
              onSitePay={45} // 
              onSitePayInsurance={63} //  (+)
              defaultPrice={55} //
              defaultPriceInsurance={73} // (+)
              days={days}
              hours={hours} />
            <PriceTable
              category=""
              cars="3,  "
              reservePrice={10}
              onSitePay={50}
              onSitePayInsurance={76}
              defaultPrice={60}
              defaultPriceInsurance={86}
              days={days}
              hours={hours} />
            <PriceTable
              category=""
              cars="6"
              reservePrice={10}
              onSitePay={59}
              onSitePayInsurance={85}
              defaultPrice={69}
              defaultPriceInsurance={97}
              days={days}
              hours={hours} />
            <PriceTable
              category="SUV"
              cars=" CX5"
              reservePrice={11}
              onSitePay={66}
              onSitePayInsurance={78}
              defaultPrice={77}
              defaultPriceInsurance={89}
              days={days}
              hours={hours} />
            <PriceTable
              category=""
              cars=""
              reservePrice={20}
              onSitePay={145}
              onSitePayInsurance={165}
              defaultPrice={165}
              defaultPriceInsurance={185}
              days={days}
              hours={hours} />
          </div>
        </div>
      </div>
      <footer style={{ backgroundColor:'#343A3F', color:'#ffffff', paddingTop:'16px', paddingBottom:'16px', fontWeight:600 }}>Copyright   2020</footer>
    </div>
  );
}

export default App;
